<template>
    <el-container>
        <el-aside width="200px">
            <el-menu>
                <el-submenu :index="item.name" v-for="item in routeData" :key="item.name">
                    <template #title>
                        <i :class="item.meta.icon"></i><span>{{item.meta.title}}</span>
                    </template>
                    <el-menu-item :index="navItem.name" v-for="navItem in item.children" :key="navItem.name">
                        <router-link :to="{name:navItem.name}">{{navItem.meta.title}}</router-link>
                    </el-menu-item>
                </el-submenu>
            </el-menu>
        </el-aside>

        <el-main>
            <el-header>
                <el-menu mode="horizontal" style="float: right">
                    <el-menu-item index="logout" @click="logout">退出登录</el-menu-item>
                </el-menu>
            </el-header>
            <router-view></router-view>
        </el-main>
    </el-container>
</template>

<script>
    export default {
        name: "AdminMain",
        data: () => ({
            routeData:[]
        }),
        methods: {
            logout(){
                // this.$store.commit("updateRouteData","[]");
                sessionStorage.removeItem("login");
                sessionStorage.removeItem("route");
                // this.$router.push({name:"login"});
                location.reload();
            }
        },
        mounted() {
            this.routeData=this.$store.state.routeData[0].children;
        }
    }
</script>

<style scoped lang="scss">
    .el-container{
        height: 100%;
        .el-menu{
            height: 100%;
            a{
                display: block;
                width: 100%;
                height: 100%;
            }
            a.router-link-exact-active{
                color: #409eff;
            }
        }
    }
</style>